export function toAE(index: number) {
  return y[x.findIndex((y) => y.indexOf(index) !== -1)];
}

export function AtoN(str: string) {
  return x[z.indexOf(str)]
}

/**
 * 
 * @param num 
 */
export function getAnimalByNum(num: number): string {
  return z[x.findIndex(ns => ns.includes(num))] || '';
}

export function EtoN(str: string) {
  switch (str) {
    case '':
      return AtoN('').concat(AtoN('')).sort()
    case '':
      return AtoN('').concat(AtoN('')).sort()
    case '':
      return AtoN('').concat(AtoN('')).sort()
    case '':
      return AtoN('').concat(AtoN('')).sort()
    case '':
      return AtoN('').concat(AtoN('')).concat(AtoN('')).concat(AtoN('')).sort()
    default:
      return []
  }
}

// 
export function CtoN(str: string) {
  return (codeClass.match(new RegExp(',' + '\\d+' + ':' + str, 'g')) || []).join('|').replace(/[,a-zA-Z:]+/g, '').split('|')
}
